import React, { useEffect, useState, useRef } from 'react'

import { useLanguage } from '../language'
import GA from '../GA'
import { useGlobal } from '../global'

const App = props => {
  const { _l } = useLanguage()
  const { _g } = useGlobal()
  const { gaId } = _g
  const ref = useRef(null)
  const [loaded, setLoaded] = useState(false)
  const { setLoading, _appID } = props

  const onLoad = () => {
    setLoading(false)
    setLoaded(true)
  }

  useEffect(() => {
    setLoading(true)
  }, [])

  return (
    <div className='tpp-app-container'>
      <GA gid={gaId} aid={_appID} />
      <iframe ref={ref}
        style={{ opacity: ~~loaded }}
        src={`/tpp/${_l('langCode') || 'en'}`}
        onLoad={onLoad}
      />
    </div>
  )
}

export default App
